import Vue from 'vue'
import App from './App.vue'
import router from './router'

// 从vant组件库导入button
import {
  Button,
  SubmitBar,
  Checkbox,
  CheckboxGroup,
  Row,
  Col,
  Stepper 
} from 'vant';

import 'lib-flexible/flexible';
// 全局注册组件
Vue
.use(Button)
.use(SubmitBar)
.use(Checkbox)
.use(CheckboxGroup)
.use(Row)
.use(Col)
.use(Stepper )


Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
