<template>
	<div class="home">

		<div class="MyCart_top clearfix">
			<div class="fl">购物车</div>
			<div class="deleteAll fr" @click="onsupervise()">{{supervise ? '完成' : '管理'}}</div>
		</div>

		<div class="MyCart_con">

			<ul>
				<li v-for="(item, index) in getdata" :key="index">
					<van-row type="flex">
						<van-col class="single" span="3">
							<van-checkbox v-model="item.checked" @change="onchecked(item)"></van-checkbox>
						</van-col>
						<van-col class="bac-auto" span="6" :style="'background-image: url('+item.url+')'">
						</van-col>
						<van-col class="goods" span="12">
							<div class="goods_title two-text">{{item.name}}</div>

              <van-row v-show="!supervise" type="flex">
								<van-col>单价: &yen;{{item.price}}</van-col>
								<van-col offset="4">数量: {{item.value}}</van-col>
							</van-row>

							<van-stepper @click="calculatedPrice()" v-show="supervise" v-model="item.value" ></van-stepper>
						</van-col>

						<van-col v-show="supervise" class="delete" span="3">
							<span @click="ondelete(index)">删除</span>
						</van-col>

					</van-row>

				</li>
			</ul>

		</div>

		<div>
			<van-submit-bar class="MyCart_foo" :price="price" button-text="提交订单" @submit="onSubmit()">
				<van-checkbox @click="onallchecked()" v-model="ischecked">全选</van-checkbox>
			</van-submit-bar>
		</div>

	</div>

</template>

<script>
	// @ is an alias to /src

	export default {
		name: 'home',
		data() {
			return {
				// 全选
				ischecked: false,
				// 管理
				supervise: false,
				request: {
					count: 10
				},
				getdata: null,
				price: 0,
			}
		},
		methods: {
			onallchecked() {
				// console.log(this.ischecked);
				for (let i in this.getdata) {
					this.getdata[i].checked = !this.ischecked;
				}
				// this.calculatedPrice();
			},

			onchecked(item) {
				// this.calculatedPrice();
				if (!item.checked) {
					this.ischecked = false;
					return;
				}

				for (let i in this.getdata) {
					if (!this.getdata[i].checked) {
						return;
					}
				}
				this.ischecked = true;
			},
			ondelete(index) {
				this.getdata.splice(index, 1);
			},
			onSubmit() {
				// console.log('提交');
			},

			onsupervise() {
				this.supervise = !this.supervise;
			},

			calculatedPrice() {
				let num = 0;
				// console.log(11)
				for (let i in this.getdata) {
					if (this.getdata[i].checked) {
						num += this.getdata[i].price * this.getdata[i].value;
					}
				}
				if (this.price != num * 100) {
					this.price = num * 100;
					// console.log('更新')
				}
			},

			ajax(data) {

				return new Promise((resolve, reject) => {

					//发起ajax请求
					//创建ajax对象
					let xhr = new XMLHttpRequest();

					xhr.onreadystatechange = function() {
						if (this.readyState === 4 && this.status === 200) {

							//成功凝固
							resolve(this.response);
						}
					}

					let params = '';

					for (let key in data) {
						params += key + '=' + data[key] + '&';
					}

					params = params.slice(0, -1);

					xhr.open('GET', 'http://api.kangliuyong.com:9002/pros?' + params, true);

					//设置服务器响应的数据类型
					xhr.responseType = 'json';

					xhr.send(null);

				})

			}
		},
		//模型侦听器
		updated() {
			this.calculatedPrice();
		},

		// 钩子
		created() {
			let self = this;
			this.ajax(this.request).then(result => {
				for (let key in result) {
					result[key].checked = false;
					result[key].value = 1;
				}
				self.getdata = result;
				// console.log(result);
			})

		}
	}
</script>

<style scoped lang="less">
	.home {
		background-color: #f4f4f4;
	}

	.MyCart_top {
		position: sticky;
		top: 0;
		left: 0;
		background-image: linear-gradient(to right, #f19436, #ed602a);
		padding: 0.4rem;
		color: #f2f2f2;
		text-align: center;
		font-size: 0.48rem;
		line-height: 1rem;
		z-index: 1;

		.deleteAll {
			font-size: 0.373rem;
		}
	}

	.MyCart_con {
		margin: 0 .2rem;
		position: relative;
		height: calc(100% - 5rem);
		width: calc(100% - .4rem);
		border-radius: 0.4rem;
		overflow-y: auto;
		padding-bottom: 1.333rem;

		ul {
			padding: 0;
			margin: 0;

			li {
				margin: .2rem 0;
				padding: .3rem;
				border-radius: .15rem;
				background-color: #fbfbfb;

				.single {
					padding-top: .8rem;
				}

				.bac-auto {
					height: 2rem;
					background-size: auto 100%;
					background-repeat: no-repeat;
					background-position: right center;
				}

				.delete {
					text-align: center;
					color: #e4393c;
				}

				.goods {
					padding: 0 .2rem;

					.goods_title {
						height: 1rem;
					}
				}
			}
		}
	}

	.MyCart_foo {
		padding-left: .2rem;
	}
</style>

<style>
	.MyCart_con li .van-icon.van-icon-success {
		border-color: #c8c9cc !important;
	}
</style>
